import { Dispatch } from 'redux';

import * as types from './actionTypes';
import { apiEndPoints, fetchApi, firebaseAPI } from '../../services/api';

const getPopularCoinsSuccess = (coins: any[]) => {
  return {
    type: types.FETCH_POPULAR_COINS_SUCCESS,
    coins,
  };
};

const getPopularCoinsFailure = (error: string) => {
  return {
    type: types.FETCH_POPULAR_COINS_FAILURE,
    error,
  };
};

export const getPopularCoins = () => {
  return async (dispatch: Dispatch<any>) => {
    try {
      const response = await fetchApi(apiEndPoints.popularCoins);
      const json = await response.json();
      if (response.ok) {
        dispatch(getPopularCoinsSuccess(json.data));
      } else {
        dispatch(
          getPopularCoinsFailure('Unable to fetch popular cryptocurrency.'),
        );
      }
    } catch (error) {
      dispatch(
        getPopularCoinsFailure('Unable to fetch popular cryptocurrency.'),
      );
    }
  };
};

const getPopularCoinsDetailsSuccess = (coinsInfo: any) => {
  return {
    type: types.FETCH_POPULAR_COINS_DETAILS_SUCCESS,
    coinsInfo,
  };
};

const getPopularCoinsDetailsFailure = (error: string) => {
  return {
    type: types.FETCH_POPULAR_COINS_DETAILS_FAILURE,
    error,
  };
};

export const getPopularCoinsDetails = (coins: any[]) => {
  return async (dispatch: Dispatch<any>) => {
    const data = coins.map((coin: any) => coin.slug);
    const requestURL = `${apiEndPoints.coinsInfo}${data.join(',')}`;
    try {
      const response = await fetchApi(requestURL);
      const json = await response.json();
      if (response.ok) {
        dispatch(getPopularCoinsDetailsSuccess(json.data));
      } else {
        dispatch(
          getPopularCoinsDetailsFailure(
            'Unable to fetch popular coins details.',
          ),
        );
      }
    } catch (error) {
      dispatch(
        getPopularCoinsDetailsFailure('Unable to fetch popular coins details.'),
      );
    }
  };
};

export const followCoinSuccess = (coins: string[]) => {
  return {
    type: types.FOLLOW_COIN_SUCCESS,
    coins,
  };
};

export const followCoinFailure = (error: string) => {
  return {
    type: types.FOLLOW_COIN_FAILURE,
    error,
  };
};

export const followCoin = (coinObj: any) => {
  return async (dispatch: Dispatch<any>, getState: any) => {
    try {
      const response = await firebaseAPI(
        getState().profile.idToken,
        'post',
        coinObj,
      );
      if (response.ok) {
        dispatch(followCoinSuccess(coinObj.followingCoins));
      } else {
        dispatch(followCoinFailure('Unable to fetch following coins.'));
      }
    } catch (error) {
      dispatch(followCoinFailure('Unable to fetch following coins.'));
    }
  };
};

export const getFollowingCoinsSuccess = (followingCoins: any[]) => {
  return {
    type: types.FETCH_FOLLOW_COINS_SUCCESS,
    followingCoins,
  };
};

export const getFollowingCoinsFailure = (error: string) => {
  return {
    type: types.FETCH_FOLLOW_COINS_FAILURE,
    error,
  };
};

export const getFollowingCoins = () => {
  return async (dispatch: Dispatch<any>, getState: any) => {
    const queryParams =
      getState().profile.idToken +
      '&orderBy="userId"&equalTo="' +
      getState().profile.userId +
      '"';
    try {
      const response = await firebaseAPI(queryParams, 'get');
      const json = await response.json();
      if (response.ok) {
        const values = Object.values(json);
        const latestValue: any = values[values.length - 1];
        console.log(latestValue);
        dispatch(getFollowingCoinsSuccess(latestValue.followingCoins));
      } else {
        dispatch(getFollowingCoinsFailure('Unable to fetch following coins.'));
      }
    } catch (error) {
      dispatch(getFollowingCoinsFailure('Unable to fetch following coins.'));
    }
  };
};

